      "author": "Kaz Voeten",
      "title": "Construction Time",
      "description": "An AR Game for young mathematics students.",
      "image": "/images/projects/finalbachelor/0.png",
      "video": "https://www.youtube.com/embed/amCZmxmb9h8",
      "explanation": "\u003Cp\u003EConstruction Time is a motivational game that keep children engaged with mathematical principles at times of frustration caused by hardships in learning at school.\u003C/p\u003E",
      "elaboration": "\u003Cp\u003EConstruction Time was created for my Final Bachelor project at the Eindhoven University of Technology. \u003Cbr/\u003E\u003Cbr/\u003EThis project aimed to provide a fun Augment Reality (AR) Game that would help children, in one way or the other, with their math education. Through exploration of the of strong technological advantage AR offers for creating a feeling of depth and scale of objects not present of the real world and listening to the desires of both teachers and students the general shape of this project continuously changed over time. Children in general expressed a discomfort with learning mathematics, especially during the transition from primary school to high school, and teachers confirmed this specifying the difficulties some students encounter as opposed to other students. The first iteration of the product designed aimed at teaching children more about three-dimensional shape. This was however changed after teachers shed light on some students’ frustration when learning these new concept at a slower pace than others, combined with difficulty proving the effect of the offered teaching alternative. This is when the product changed development direction and became a tool for motivating children who get frustrated (even in class) when working on identifying two dimensional drawings as three dimensional shapes in order to perform calculations. The game offers missions, that the children reported they liked more than just identifying shape, to keep the players engaged in a positive way when they frustration stops them from learning. This while still interacting with concepts related to the mathematical subject they are having trouble with, albeit in a more practical fashion. \u003C/p\u003E",
      "gallery": "/images/projects/finalbachelor/0.png,/images/projects/finalbachelor/1.png,/images/projects/finalbachelor/7-3.jpg",
      "views": 0,
      "created_at": "2017-12-30 08:14:09",
      "updated_at": "2021-03-05 12:07:00"
