      "author": "Kaz Voeten",
      "title": "Horus: An Egyptian Puzzle Game",
      "description": "Horus is a short first-person puzzle game with unique ambiance.",
      "image": "https://api.kazvoeten.com/project/image/6-0.jpg",
      "video": "https://www.youtube.com/embed/hPrMLveVTQ0",
      "explanation": "\u003Cp\u003E It is unlike anything I have ever seen. What was first hidden underneath the sand has surfaced, ready to be explored. The rest of my team didn’t agree on going, so I will just do it myself. I wonder how old this place is, and what I will discover...\u003C/p\u003E",
      "elaboration": "\u003Cp\u003EDesign by: Kaz Voeten, Ralf Schmidt, Illona Hoenderop, Jet Kooijman, Metchell Pijl\u003Cbr/\u003E\u003Cbr/\u003EHorus was created for the course \"Design for Games and Play I: Game Designer\" at the Eindhoven University of Technology. \u003Cbr/\u003E\u003Cbr/\u003EImagine that you are an archaeologist and you have heard rumours that a new pyramid is discovered. Through ancient history, there have been some stories about this special pyramid named after Horus, the God of the heavens. Of course, you are really excited about this discovery and want to be the first one to enter the pyramid, but once you do, you realise that you are trapped inside. The only way forward is deeper into the tomb. You have to face all kind of obstacles and survive to (hopefully) discover a way to get out.\u003Cbr/\u003E\u003Cbr/\u003EDuring the first few levels, the obstacles are relatively easy. The goal of these levels is not to complete them, but to learn the controls and mechanics of the game. Therefore, all the aspects and mechanics are introduced during these levels and may come back later in the game. Later on, the player has to combine those aspects to complete more challenging levels.\u003Cbr/\u003E\u003Cbr/\u003EHorus was my first experience with a full game-design project cycle where we set out project goals, designed all aspects of the game, and then realized our creative idea in Unity.\u003C/p\u003E",
      "gallery": "https://api.kazvoeten.com/project/image/6-0.jpg,https://api.kazvoeten.com/project/image/6-1.jpg,https://api.kazvoeten.com/project/image/6-2.jpg,https://api.kazvoeten.com/project/image/6-3.jpg",
