export default {
  getNumPages() {
    return Math.max(1, Math.floor(projectData.length / pageLimit));
  },
  getLatest () {
    return projectData.slice(0, Math.min(projectData.length - 1, pageLimit));
  },
  getPage (id) {
    let idx = id -1;
    if (idx - 1 > this.getNumPages()) {
      return this.getLatest();
    }
    return projectData.slice(idx * pageLimit, Math.min(projectData.length - 1, idx * pageLimit + pageLimit));
  },
  getArticle (id) {
    return projectData[id];
  },
  getDate (timestamp) {
    const date = Date.parse(timestamp)
    const dtf = new Intl.DateTimeFormat('en', { year: 'numeric', month: 'short', day: '2-digit' }) 
    const [{ value: mo }, , { value: da }, , { value: ye }] = dtf.formatToParts(date) 
    return `${da} ${mo} ${ye}`
  }
}
