<template>
  <v-container>
    <h1>My Projects</h1>
    <p>This page is work in progress, older projects will be added soon.</p>
    <v-row 
      class="mb-3"
    >
      <v-col
        v-for="card in cards"
        :key="card.id"
        :lg="3"
        :md="6"
        :sm="6"
        :xs="6"
      >
        <v-card
          shaped
          :loading="card.loading"
          :disabled="card.disabled"
          class="mx-auto"
          max-height="380px"
        >
          <v-img
            class="white--text align-end"
            height="150px"
            :src="card.image"
          >
          </v-img>

          <v-card-title style="white-space: nowrap; overflow: hidden;text-overflow: ellipsis;">{{card.title}}</v-card-title>

          <v-card-subtitle class="text-left">{{card.date}}</v-card-subtitle>

          <v-card-text class="text-left" style="height:50px;overflow: hidden;text-overflow: ellipsis;">
            {{card.desc}}
          </v-card-text>

          <v-card-actions>
            <v-btn
              color="orange"
              text
              :to="card.target"
            >
              READ MORE
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-pagination
      v-model="page"
      :length="length"
      :page="page"
      color="#e4b040"
      @input="setPage"
    ></v-pagination>
  </v-container>
</template>

<script>
import projects from '@/services/projects'

export default {
  components: {

  },
  computed: {
    cols () {
      const { xl, lg, md, sm } = this.$vuetify.breakpoint
      if (xl || lg) return 4
      else if (md) return 3
      else if (sm) return 2
      return 1
    }
  },
  data: () => ({
    page: 1,
    length: 1,
    cards: []
  }),
  methods: {
    async setPage (page) {
      this.getNews(page)
    },
    async getNews (page) {
      this.cards = []
      const articles = projects.getPage(page);
      for (let i = 0; i < articles.length; ++i) {
        const article = articles[i];
        this.cards.push({
          id: i,
          title: article.title,
          date: projects.getDate(article.created_at),
          image: article.image,
          desc: article.description,
          target: `project/${i}`,
          loading: false,
          disabled: false
        })
      }
    }
  },
  async mounted () {
    this.page = 1;
    this.length = projects.getNumPages();
    this.setPage(this.page);
  }
}
</script>

